<template>
  <div>
    <el-row>
      <el-col><el-button @click="addRenZhe()">添加忍者</el-button></el-col>
    </el-row>

    <el-table :data="tableData" style="width: 100%">
      <el-table-column
        prop="name"
        label="忍者名称"
        width="180"
      ></el-table-column>
      <el-table-column
        prop="fight"
        label="忍者战力"
        width="180"
      ></el-table-column>
      <el-table-column
        prop="level"
        label="忍者级别"
        width="180"
      ></el-table-column>
      <el-table-column fixed="right" label="操作"></el-table-column>
      <el-table-column>
        <template slot-scope="scope">
          <el-button
            type="text"
            size="small"
            circle="true"
            @click="deleteRenZhe(scope.row.id)"
            >删除忍者</el-button
          >
          <el-button
            type="text"
            size="small"
            circle="true"
            @click="modifyRenZhe(scope.row.id)"
            >修改忍者</el-button
          >
          <el-button
            type="text"
            size="small"
            circle="true"
            @click="studySkill(scope.row.id)"
            >学习技能</el-button
          >
          <el-button
            type="text"
            size="small"
            circle="true"
            @click="scanSkill(scope.row.id)"
            >查看技能</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="技能学习" :visible.sync="dialogFormVisible">
      <el-form :model="form">
        <el-form-item label="技能名称" :label-width="formLabelWidth">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>
         <el-form-item label="技能等级" :label-width="formLabelWidth">
          <el-input v-model="form.level" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="study()">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="修改忍者" :visible.sync="dialogFormVisible2">
      <el-form :model="form">
        <el-form-item label="忍者名" :label-width="formLabelWidth">
          <el-input v-model="form2.rname" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="忍者战力" :label-width="formLabelWidth">
          <el-input v-model="form2.rfight" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="忍者等级" :label-width="formLabelWidth">
          <el-input v-model="form2.rlevel" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="modify()">确 定</el-button>
      </div>
    </el-dialog>

    <el-dialog title="添加忍者" :visible.sync="dialogFormVisible3">
      <el-form :model="form">
        <el-form-item label="忍者名" :label-width="formLabelWidth">
          <el-input v-model="form3.rname" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="忍者战力" :label-width="formLabelWidth">
          <el-input v-model="form3.rfight" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="忍者等级" :label-width="formLabelWidth">
          <el-input v-model="form3.rlevel" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible3 = false">取 消</el-button>
        <el-button type="primary" @click="add()">确 定</el-button>
      </div>
    </el-dialog>

    <el-dialog title="技能信息" :visible.sync="dialogTableVisible">
      <el-table :data="gridData">
        <el-table-column
          property="name"
          label="技能名"
          width="150"
        ></el-table-column>
        <el-table-column
          property="level"
          label="技能等级"
          width="150"
        ></el-table-column>
      </el-table>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "showRenzhe",
  methods: {
    studySkill(id) {
      this.dialogFormVisible = true;
      this.form.id = id;
      // this.study();
    },
    study() {
      // console.log("调用study");
      var that = this.tableData;
      // console.log("调用study1");
      for (var i = 0; i < that.length; i++) {
        // console.log("调用study1");
        if (that[i].id == this.form.id) {
          that[i].skill.push({ name: this.form.name ,level:this.form.level});
          this.dialogFormVisible = false;
          alert(that[i].name + "成功学习技能:" + this.form.name);
          // console.log("111",this.tableData);
          return;
        }
      }
    },
    modifyRenZhe(id) {
      this.dialogFormVisible2 = true;
      this.form2.id = id;
      // this.modify();
    },
    modify() {
      var that = this.tableData;
      for (var i = 0; i < that.length; i++) {
        if (that[i].id == this.form2.id) {
          that[i].name = this.form2.rname;
          that[i].fight = this.form2.rfight;
          that[i].level = this.form2.rlevel;
          this.dialogFormVisible2 = false;
          // alert("成功修改");
          return;
        }
      }
    },
    addRenZhe() {
      // console.log('调用addRenZhe');
      this.dialogFormVisible3 = true;
    },
    add() {
      // console.log('调用add');
      this.tNum++;
      var t = { id: "", name: "", fight: "", level: "", skill: "" },
        that = this.form3;
      //  console.log('调用add');

      t.id = this.tNum;
      t.name = that.rname;
      t.fight = that.rfight;
      t.level = that.rlevel;
      t.skill = [];
      console.log("调用add");
      this.tableData.push(t);
      this.dialogFormVisible3 = false;
      // alert("成功添加");
    },
    deleteRenZhe(id) {
      var that = this.tableData;
      for (var i = 0; i < that.length; i++) {
        if (that[i].id == id) {
          that.splice(i, 1);
        }
      }
      alert("成功删除");
    },
    scanSkill(id){
      var that=this.tableData;
      for(var i=0;i<that.length;i++){
        if(that[i].id==id){
          this.gridData=that[i].skill;
        }
      }
      this.dialogTableVisible=true;
    }
  },
  mounted() {},
  data() {
    return {
      //{sdept,smajor,sclass,sname}
      tNum: 5,
      tableData: [
        { id: 0, name: "漩涡鸣人(幼年)", fight: 20, level: "C", skill: [] },
        { id: 1, name: "宇智波佐助(幼年)", fight: 20, level: "C", skill: [] },
        { id: 2, name: "小樱(幼年)", fight: 20, level: "C", skill: [] },
        { id: 3, name: "卡卡西(青年)", fight: 100, level: "A", skill: [{name:"雷切",level:"S"},{name:"螺旋丸",level:"S"}] },
        { id: 4, name: "宇智波佐助(博人传)", fight: 20, level: "D", skill: [] },
      ],
      // bridgename: "",
      tName: "",
      tLevel: "",
      tFight: "",
      tSkill: "",
      dialogFormVisible: false,
      dialogFormVisible2: false,
      dialogFormVisible3: false,
      form: {
        id: "",
        name: "",
        level:"",
        delivery: false,
        type: [],
      },
      form2: {
        id: "",
        rname: "",
        rfight: "",
        rlevel: "",
      },
      form3: {
        id: "",
        rname: "",
        rfight: "",
        rlevel: "",
      },
      gridData: [],
      dialogTableVisible: false,
    };
  },
};
</script>